import {
  trigger,
  animate,
  transition,
  style,
  query
} from "@angular/animations";

export const routerAnimation = trigger("routerAnimation", [
  transition("* => *", [
    // style({ position: "relative" }),
    query(":enter", [style({ left: "50px", opacity: 0 })], { optional: true }),
    query(":enter", [animate("1s ease-out", style({ left: "68px", opacity: 1 }))], { optional: true })
  ])
]);
