import { Injectable } from "@angular/core";
import { LifePeriod } from "src/app/models";
import { Observable } from "rxjs";
import { AngularFirestore } from "@angular/fire/firestore";
import { DateService } from "../date/date.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root"
})
export class WorkService {
  get work$(): Observable<LifePeriod[]> {
    return this.firestore
      .collection<LifePeriod>("work_experience")
      .valueChanges()
      .pipe(
        map(payload =>
          payload.sort((a, b) =>
            this.toCode(b.period.start) > this.toCode(a.period.end)
              ? 1
              : this.toCode(a.period.start) > this.toCode(b.period.end)
              ? -1
              : 0
          )
        )
      );
  }

  constructor(
    private firestore: AngularFirestore,
    private dateService: DateService
  ) {}

  private toCode(date: string): Number {
    return this.dateService.convertStringDateToNumber(date);
  }
}
