import { Component, OnInit, Input } from '@angular/core';
import { Observable } from 'rxjs';

import { TechService } from 'src/app/services/tech/tech.service';
import { Tech, Category } from 'src/app/models';

@Component({
  selector: 'tech-list',
  templateUrl: './tech-list.component.html',
  styleUrls: ['./tech-list.component.scss']
})
export class TechListComponent implements OnInit {
  @Input() category: Category;
  techList: Tech[];

  constructor(
    private techService: TechService
  ) { }

  ngOnInit() {
    this.techService.tech$.subscribe(list => {
      this.techList = list.filter(x => x.categoryKey == this.category.key).sort((a,b) => (a.order > b.order) ? 1 : ((b.order > a.order) ? -1 : 0));
    })
  }
}
