import { Injectable } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class DateService {
  constructor() {}

  convertStringDateToNumber(date: string): number {
    const parts = date.split(" ");
    return Number(parts[1]) * 100 + this.monthToNr(parts[0]);
  }

  private monthToNr(month: string): number {
    switch (month) {
      case "Januari":
        return 1;
      case "Februari":
        return 2;
      case "Maart":
        return 3;
      case "April":
        return 4;
      case "Mei":
        return 5;
      case "Juni":
        return 6;
      case "Juli":
        return 7;
      case "Augustus":
        return 8;
      case "September":
        return 9;
      case "Oktober":
        return 10;
      case "November":
        return 11;
      case "December":
        return 12;
    }
  }
}
